.form {
  margin-top: 30px;
  display: grid;
  grid-template: 45px 190px 45px / repeat(2, 280px);
  justify-content: space-between;
  row-gap: 30px;
  &__group {
    position: relative;
  }
  &__label {
    position: absolute;
    display: block;
    height: 18px;
    top: -9px;
    left: 12px;
    padding: 0 8px;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #000000;
    background-color: #fff;
    z-index: 1;
    &-check {
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: #000000;
    }
  }
  &__input {
    padding: 0 20px;
    width: 100%;
    height: 100%;
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #000000;
  }
  &__textarea {
    position: relative;
    grid-column: 1/3;
    textarea {
      width: 100%;
      height: 100%;
      padding: 20px;
      resize: none;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-column: 1/3;
  }
}
