.contact {
  padding: 120px 0 90px 0;
  &__wrapper {
    padding: 0 35px 0 52px;
    display: grid;
    grid-template-columns: 400px 585px;
    justify-content: space-between;
  }

  &__img {
    img {
      height: 100%;
    }
  }

  &__title {
    position: relative;
    padding-left: 12px;
    @include circle;
  }
  .divider {
    margin: 20px 0 0 0;
  }
  &__text {
    margin-top: 20px;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }
  &__social {
    &-list {
      margin-top: 20px;
      display: flex;
      align-items: center;
    }
  }
}

.social {
  &__item {
    &:not(:last-child) {
      margin-right: 25px;
    }
  }
  &__link {
    svg {
      width: 30px;
      height: 30px;
      fill: #000;
    }
  }
}
